<template>
  <div style="text-align:center;">
    <h3 class="mdl-color-text--primary">Add Product</h3>
    <product-form ref="form">
    </product-form>
    <router-link
      tag="a"
      class="custom-button"
      :to="{ name: 'products.listView' }"
    >
     Back
    </router-link>
    <button
      class="mdl-button mdl-js-button mdl-button--raised mdl-js-ripple-effect mdl-button--colored custom-button--primary"
      @click="createProduct">Add</button>

  </div>
</template>
<script>
import { ProductForm } from '.'
import { mapActions } from 'vuex'
import { MESSAGES } from '@/constants'

export default {
  components: {
    ProductForm
  },
  computed: {
    form: {
      get () {
        return this.$refs.form
      }
    }
  },
  methods: {
    ...mapActions([
      'addProduct'
    ]),
    createProduct () {
      this.form.valid().then(valid => {
        if (valid) {
          this.addProduct(this.form.productParams).then(() => {
            this.$toastr.s(MESSAGES.success_add)
            this.$router.push({ name: 'products.listView' })
          }).catch(e => {
            this.$toastr.e(MESSAGES.form_verify)
          })
        }
      })
    }
  }
}
</script>



